.formContainer{
    height: 100vh;
    width: 100%;
    background-color: hotpink;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

form{
    height: 60vh;
    width: 40vw;
    display: flex;
    flex-direction: column;
    background-color: aliceblue;
    padding: 5rem;
}
form input{
    width: 15rem;
    height: 1.5rem;

}
h4{
    text-align: center;
    margin-top: -10px;
}
.Link{
    margin-left: 10rem;
}
form button{
    width: 6rem;
    height: 1.5rem;
    background-color: rgb(223, 35, 129);
    border: 1px goldenrod solid;
    margin-top: 1rem;
    color: aliceblue;
    font-size: 1rem;
}